import React, { useState } from 'react';
import './ReflectionInput.css';

function ReflectionInput({ emotion, situation, reaction, onSubmit, onBack }) {
  const [reflection, setReflection] = useState('');

  const handleSubmit = () => {
    if (reflection.trim()) {
      onSubmit && onSubmit(reflection);
    }
  };

  return (
    <div className="reflection-input">
      <h2> ,    ?</h2>
      
      <div className="context">
        <p><strong>:</strong> {emotion}</p>
        <p><strong>:</strong> {situation}</p>
        <p><strong> :</strong> {reaction}</p>
      </div>

      <textarea
        placeholder="       ?"
        value={reflection}
        onChange={(e) => setReflection(e.target.value)}
      />
<div className="button-group">
  <button onClick={onBack}> </button>
  <button onClick={handleSubmit} disabled={!reflection}> </button>
</div>
    </div>
  );
}

export default ReflectionInput;